.home-header-container {
    height          : 60px;
    width           : 100%;
    padding         : 0 40px;
    position        : sticky;
    top             : 0;
    background-color: white;
    z-index         : 100;

    .home-header-content {
        width  : 100%;
        height : 100%;
        display: flex;

        .left-content {
            width      : 20%;
            display    : flex;
            align-items: center;

            .header-logo {
                height     : 100%;
                width      : 100%;
                cursor     : pointer;
                transform  : scale(0.5, 0.5);
                margin-left: -45px;
            }

            i {
                font-size: 25px;
                color    : grey;
                cursor   : pointer;
            }
        }

        .center-content {
            display        : flex;
            justify-content: space-between;
            width          : 55%;
            align-content  : center;
            margin         : auto 0;

            .child-content {
                .subs-title {
                    font-weight: 400;
                    font-size  : 12px;
                }
            }
        }

        .right-content {
            width          : 25%;
            display        : flex;
            justify-content: flex-end;
            align-items    : center;

            .support {
                padding-right: 50px;
                display      : flex;
                align-items  : center;
                cursor       : pointer;

                i {
                    font-size   : 15px;
                    margin-right: 5px;
                }
            }

            .language-vi {
                opacity: 0.2;
                cursor : pointer;

                &.active {
                    opacity: 1;

                    span {
                        color: orange;
                    }
                }

                &:hover {
                    opacity: 1;
                }
            }

            .language-en {
                opacity: 0.2;
                margin : 0 10px;
                cursor : pointer;

                &.active {
                    opacity: 1;

                    span {
                        color: blue;
                    }
                }

                &:hover {
                    opacity: 0.8;
                }
            }

            // i {
            //     color: #00ebff;

            // }
        }
    }
}

.home-header-banner {
    background-image   : url('../../assets/header-background.jpg');
    height             : 1050px;
    background-repeat  : no-repeat;
    background-size    : cover;
    background-position: center;
    flex-direction     : column;

    .content-up {
        background-image: linear-gradient(rgba(126, 126, 126, 0.25), rgba(255, 255, 255, 0.1));
        height          : 65%;
        padding-top     : 15px;

        .title1 {
            text-align   : center;
            font-size    : 30px;
            text-shadow  : 1px 1px 1px #333;
            margin-bottom: 5px;
            color        : white;
            margin-top   : 30px;
        }

        .title2 {
            text-align   : center;
            font-size    : 30px;
            text-shadow  : 1px 1px 1px #333;
            color        : white;
            margin-bottom: 15px;
        }

        .search {
            width           : fit-content;
            display         : flex;
            justify-content : center;
            align-items     : center;
            margin          : 0 auto;
            padding         : 10px;
            border-radius   : 25px;
            background-color: #f7d800;

            i {
                margin: 0 5px;
            }

            input {
                height    : 30px;
                width     : 360px;
                background: transparent;
                border    : none;
                outline   : none;
            }
        }
    }

    .content-down {
        padding-bottom  : 10px;
        height          : 35%;
        display         : flex;
        align-items     : flex-end;
        justify-content : center;
        background-image: linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 1));

        .options {
            text-align     : center;
            display        : flex;
            gap            : 50px;
            justify-content: center;

            .option-child {
                width: 100px;

                .icon-child {
                    background   : white;
                    width        : 40px;
                    height       : 40px;
                    padding      : 10px;
                    border-radius: 50%;
                    text-align   : center;
                    margin       : 0 auto;

                    i {
                        color    : #49bce2;
                        font-size: 20px;
                    }
                }
            }

            .text-child {
                font-size  : 17px;
                font-weight: 500;
                cursor     : pointer;

                &:hover {
                    color: #49bce2
                }
            }
        }
    }
}