#TableManageUser {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  #TableManageUser td, #TableManageUser th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  #TableManageUser tr:nth-child(even){background-color: #f2f2f2;}
  
  #TableManageUser tr:hover {background-color: #ddd;}
  
  #TableManageUser th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #04AA6D;
    color: white;
  }


  .btn-edit {
    width: 50px;
    border: none;
    outline: none;
    background: transparent;
    color: orange;
    &:hover {
        background-color: transparent !important;
        color: orange !important;
    }


}
.btn-delete {
    width: 50px;
    border: none;
    outline: none;
    background: transparent;
    color: red;
    &:hover {
        background-color: transparent !important;
        color: red !important;
    }
}

.modal-user-container {
    .modal-content{
        .modal-header {
            button {
                background-color: transparent !important;
                color: white;
                border: none;
                font-size: 27px;
            }
        }
    }

    .modal-user-body{
        display: flex;
        gap: 15px;
        flex-wrap: wrap;
        .input-container {
            display: flex;
            flex-direction: column;
            width: 49%;

            &.max-width-input {
                width: 100%;
            }
            input {
                border-radius: 3px;
                height: 30px;
                border: 1px solid grey;
                outline: none;
                padding: 0 10px;
            }
        }
    }
}