.doctor-detail-container {
    height: 500px;

    .intro-doctor {
        display: flex;

        .content-left {
            width              : 20%;
            // background     : center center no-repeat;
            background-size    : cover;
            background-repeat  : no-repeat;
            background-position: center;
            width              : 120px;
            height             : 120px;
            border-radius      : 50%;
            margin             : 10px 20px;
            // background-size: contain;
        }

        .content-right {
            width         : 80%;
            display       : flex;
            flex-direction: column;
            padding-left  : 10px;

            .up {
                padding-top: 15px;
                font-size  : 20px;
                font-weight: 600;
            }

            .down {
                padding-top: 10px;

                .like-share-plugin {
                    margin: 10px 0 20px 0;
                }
            }
        }
    }

    .schedule-doctor {
        margin    : 0 100px;
        display   : flex;
        padding   : 10px 0;
        min-height: 200px;

        .content-left {
            width: 50%;
        }

        .content-right {
            width: 50%;
        }
    }

    .detail-infor-doctor {
        margin-top      : 20px;
        padding-top     : 20px;
        padding-left    : 10px;
        padding-right   : 10px;
        background-color: #f9f9f9;
        padding         : 10px 100px;
        border-top      : 1px solid #ddd;
        border-bottom   : 1px solid #ddd;

        img {
            width: 100%;
        }

        h1,
        h2,
        h3 {
            font-size: 17px;
        }
    }

    .comment-doctor {
        padding: 10px 100px;
    }

}

.live-chat-container {
    position: fixed;
    bottom  : 0;
    right   : 0;
}